/****************************************/
/********** ANIMATION DURATION **********/
/****************************************/

/*
1. TN
2. SM
3. MD
4. LG
5. WD
*/


/**************/
/***** TN *****/
*:hover > [class*="anima"][class*="into"][class*="tn"],
*:focus > [class*="anima"][class*="into"][class*="tn"],
*:active > [class*="anima"][class*="into"][class*="tn"],
[class*="anima"][class*="local"][class*="tn"],
[class*="anima"][class*="onto"][class*="tn"]:hover,
[class*="anima"][class*="onto"][class*="tn"]:focus,
[class*="anima"][class*="onto"][class*="tn"]:active,
[class*="anima"][class*="global"][class*="tn"] > *,
[class*="anima"][class*="toall"][class*="tn"]:hover > *,
[class*="anima"][class*="toall"][class*="tn"]:focus > *,
[class*="anima"][class*="toall"][class*="tn"]:active > *,
[class*="anima"][class*="fromall"][class*="tn"] > *:hover,
[class*="anima"][class*="fromall"][class*="tn"] > *:focus,
[class*="anima"][class*="fromall"][class*="tn"] > *:active,
[class*="anima"][class*="dio"][class*="tn"]:checked ~ [class*="anima"][class*="target"][class*="tn"] {
    animation-duration: var(--anima-duration-tn);
}


