/*************************************/
/********** ANIMATION DELAY **********/
/*************************************/

/*
1. Small
2. Medium
3. Large
*/


/*****************/
/***** Small *****/
[class*="animate-"][class*="small"]:hover,
[class*="animate-"][class*="small"]:focus,
[class*="animate-"][class*="small"]:active,
*:hover > [class*="animated-"][class*="small"],
*:focus > [class*="animated-"][class*="small"],
*:active > [class*="animated-"][class*="small"],
[class*="radio-"][class*="small"]:checked ~ [class*="target-"][class*="small"],
[class*="show-"][class*="small"],
[class*="shown-"][class*="small"] > * {
    animation-delay: var(--anima-delay-small);
}


/******************/
/***** Medium *****/
[class*="animate-"][class*="medium"]:hover,
[class*="animate-"][class*="medium"]:focus,
